.popup {
    visibility: hidden;
    opacity: 0;
    transition: visibility .3s,opacity .3s ease;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: rgba(0, 0, 0, .5);
    color: black;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
}